import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/components/Layout'
import Gobang from '@/components/Layout/gobang'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Layout',
      component: Layout,
      children:[
        {
          name:'gobang',
          path:'gobang',
          component:Gobang
        },
      ]
    }
  ]
})



