<template>
  <div>
    <div class="index-left">
      <p>应用</p>
      <router-link :to="func.url" tag="div" :key="index" v-for="(func,index) in funList" class="func">
        {{func.name}}
      </router-link>
    </div>
    <div class="index-right">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Layout',
  data () {
    return {
      funList:[
        {
            name:'简单五子棋',
            url:'/gobang'
        },
        {
          name:'111',
          url:'/'
        },
      ]
    }
  }
}
</script>

<style scoped>
  .index-left {
    margin: 20px 20px 0 50px;
    background-color: #fff;
    height: 500px;
    width: 300px;
    float: left;
  }
  .index-left>p {
    padding: 10px;
    background-color: #42b19e;
    color: #fff;
  }
  .func {
    text-align: center;
    height: 40px;
    line-height: 40px;
    margin-bottom: 3px;
  }
  .func:hover {
    background-color: #999;
    color: #fff;
  }
  .index-right {
    float: left;
    margin-top: 20px;
    background-color: #fff;
    margin-left: 30px;
  }
  .router-link-exact-active {
    background-color: #999;
    color: #fff;
  }
</style>



